<template>
    <n-grid cols="1" item-responsive>
        <n-grid-item>
            <!-- ActionBar  -->
            <n-el tag="div" class="action-bar">
                <!-- Menu  -->
                <div class="menu-btn" v-if="menuEnable">
                    <!-- Menu  -->
                    <n-button @click="menuClick" style="width:var(--height-medium);height:var(--height-medium);" quaternary circle>
                        <!-- Menu  -->
                        <template #icon>
                            <n-icon>
                                <menu-icon v-show="menuStatus" />
                                <menu-un-icon v-show="!menuStatus" />
                            </n-icon>
                        </template>
                    </n-button>
                </div>
                <!-- title  -->
                <div class="title-box" :style="!menuEnable ? 'padding-left: 16px' : ''">
                    <!--  -->
                    <n-breadcrumb>
                        <n-breadcrumb-item :key="index" v-for="(item,index) in title">
                            <n-dropdown :options="item.list">
                                <div class="trigger">
                                    {{item.lable}}
                                </div>
                            </n-dropdown>
                        </n-breadcrumb-item>
                    </n-breadcrumb>
                </div>
                <!--  -->
                <div class="action-other">
                    <!--  -->
                    <slot></slot>
                </div>
            </n-el>
        </n-grid-item>
    </n-grid>
</template>

<script setup>
//  vue
import { ref, toRefs } from 'vue'
// naive ui
import {
    NGrid,NGridItem,NEl,NDivider,NIcon,NButton,NBreadcrumb,
    NBreadcrumbItem,NDropdown,
} from 'naive-ui'

//  vicon 
import {
    MenuFoldOutlined as MenuIcon,
    MenuUnfoldOutlined as MenuUnIcon,
} from '@vicons/antd'

// props 
const props = defineProps({
    title: { // 
        type: Array, //  Array 
        default: [{
            lable: '',
            list: [
                {
                    label: "",
                    key: 1
                },
                {
                    label: "",
                    key: 2
                }
            ]
        }] // 
    },
    menuStatus: { // 
        type: Boolean, //  Boolean 
        default: true // 
    },
    menuEnable: { // 
        type: Boolean, //  Boolean 
        default: true // 
    },
})

// 
const { title, menuStatus, menuEnable } = toRefs(props);

// 
const emits = defineEmits(['menuClick'])
// Menu 
const menuClick = () => {
    emits('menuClick')
}

// Menu 

</script>

<!--  -->
<script>
export default {
    name: "ActionBar"
}
</script>

<style scoped>
.action-bar {
    display: flex;
    flex-direction: row;
    align-items: center;
    height: var(--height-large);
    background-color: var(--card-color);
    border-bottom: 1px solid var(--divider-color);
}

.menu-btn {
    width: var(--height-large);
    height: var(--height-large);
    display: flex;
    justify-content: center;
    align-items: center;
}

.trigger {
  padding: 4px;
  margin: -4px;
  border-radius: inherit;
}

.title-box {
    max-width: 320px;
}

.action-other {
    box-sizing: border-box;
    padding-right: 16px;
    flex: 1;
    display: flex;
    justify-content: right;
    align-items: center;
}
</style>