<template>
    <n-el tag="div" class="app">
        <div class="left">
            <n-drawer v-model:show="leftOpen" :width="leftWidth" placement="left" v-if="!leftStatus">
                <n-layout :inverted="leftBlack" has-sider style="height: 100vh">
                    <n-layout-sider :inverted="leftBlack" bordered collapse-mode="width" :collapsed-width="64"
                        :width="leftWidth" :collapsed="!leftOpen">
                        <n-scrollbar>
                            <n-menu ref="menuInstRef" v-model:value="selectedKey" :inverted="leftBlack" accordion
                                :collapsed="!leftOpen" :options="menuOptions" :collapsed-width="64"
                                :collapsed-icon-size="22" />
                        </n-scrollbar>
                    </n-layout-sider>
                </n-layout>
            </n-drawer>

            <n-layout :inverted="leftBlack" has-sider v-else style="height: 100vh">
                <n-layout-sider :inverted="leftBlack" bordered collapse-mode="width" :collapsed-width="64"
                    :width="leftWidth" :collapsed="!leftOpen">
                    <n-scrollbar style="max-height: 100vh">
                        <n-menu ref="menuInstRef" v-model:value="selectedKey" :inverted="leftBlack" accordion
                            :collapsed="!leftOpen" :options="menuOptions" :collapsed-width="64"
                            :collapsed-icon-size="22" />
                    </n-scrollbar>
                </n-layout-sider>
            </n-layout>
        </div>
        <div class="right">
            <ActionBar :title="title" @menuClick="leftOpen = !leftOpen" :menuStatus="leftOpen" :menuEnable="true">
                <!--  -->
                <n-button @click="searchModal = true" style="width:var(--height-medium);height:var(--height-medium);"
                    quaternary circle>
                    <!--  -->
                    <template #icon>
                        <n-icon>
                            <search-icon />
                        </n-icon>
                    </template>
                </n-button>
                <!-- Dark  -->
                <n-button @click="dark = !dark"
                    style="width:var(--height-medium);height:var(--height-medium);margin-left:6px;" quaternary circle>
                    <!--  -->
                    <template #icon>
                        <n-icon>
                            <moon-icon v-show="!dark" />
                            <sunny-icon v-show="dark" />
                        </n-icon>
                    </template>
                </n-button>
            </ActionBar>
            <!--  -->
            <n-scrollbar style="flex: 1;">
                <router-view v-slot="{ Component }">
                    <transition name="aside-right" mode="out-in">
                        <component :is="Component" />
                    </transition>
                </router-view>
            </n-scrollbar>
        </div>
    </n-el>

    <n-modal v-model:show="searchModal" preset="card" style="width: 400px" title="">

        <n-input v-model:value="searchText" style="margin-top: 16px;" placeholder="">
            <template #prefix>
                <n-icon :component="SearchIcon" />
            </template>
        </n-input>

        <n-list style="margin-top: 10px;" hoverable clickable>
            <n-list-item :key="index" v-for="(item,index) in searchList">
                <router-link style="text-decoration: none;" :to="item.to">
                    <n-thing @click="searchModal = false" :title="item.name" content-style="margin-top: 10px;" />
                </router-link>
            </n-list-item>
        </n-list>

        <template #footer>
            <div style="width: 100%;text-align:right;">
                <n-button @click="searchModal = false"></n-button>
            </div>
        </template>

    </n-modal>

</template>

<script setup>
//  vue
import { ref, h, getCurrentInstance, watch, inject, provide } from 'vue'
//  naive ui 
import {
    NButton, NSpace, NGrid, NGridItem, NEl,
    NIcon, NMenu, NLayoutSider, NLayout, NDrawer,
    NDrawerContent, NScrollbar, NAvatar, NModal,
    NInput, NList, NListItem, NThing, useLoadingBar
} from 'naive-ui'
//  
import ActionBar from '@/components/ActionBar.vue'
// 
import { RouterLink, useRouter } from 'vue-router'
// 
import {
    Home20Regular as HomeIcon,
    Home20Regular as AcBcDcIcon,
    Building20Regular as Home20Regular,
    Money20Regular as MoneyIcon,
    BezierCurveSquare20Regular as BezierCurveSquareIcon,
    VehicleCarProfileLtr20Regular as VehicleCarProfileLtrIcon,
    CalendarEdit20Regular as CalendarEditIcon,
    ShoppingBag20Regular as ShoppingBagIcon,
    DeveloperBoard20Regular as DeveloperBoardIcon,
    Syringe20Regular as SyringeIcon,
    WeatherSunny24Regular as SunnyIcon,
    WeatherMoon16Regular as MoonIcon,
    Search32Regular as SearchIcon
} from "@vicons/fluent"

// 
function renderIcon(icon) {
    return () => h(NIcon, null, { default: () => h(icon) });
}

// 
const selectedKey = ref("home");
const menuInstRef = ref(null);
const selectAndExpand = (key) => {
    selectedKey.value = key;
    menuInstRef.value?.showOption(key);
};
const title = ref([{
    lable: ''
}])

const router = useRouter();
const loadingBar = useLoadingBar()

router.beforeEach((to, from, next) => {
    document.title = to.meta.title
    loadingBar.start();
    next();
});

router.afterEach((to, from) => {
    loadingBar.finish()
})

selectAndExpand(useRouter().currentRoute._rawValue.name);

// 
getCurrentInstance()?.appContext.config.globalProperties.$router.beforeEach((to, from, next) => {
    // 
    selectAndExpand(to.name);
    // 
    updateTitle(to)
    // 
    next();
})

// 
const menuOptions = [
    // 
    {
        label: () => h(RouterLink, { to: '/' }, { default: () => '' }),
        key: "home",
        icon: renderIcon(HomeIcon)
    },
    // 
    {
        label: '',
        key: "realEstate",
        icon: renderIcon(Home20Regular),
        children: [
            {
                label: () => h(RouterLink, { to: '/realestate/housingmanagement' }, { default: () => '' }), key: "housingManagement",
            },
            {
                label: () => h(RouterLink, { to: '/realestate/ownerinformation' }, { default: () => '' }), key: "ownerInformation",
            },
            {
                label: () => h(RouterLink, { to: '/realestate/ownermember' }, { default: () => '' }), key: "ownerMember",
            },
            {
                label: () => h(RouterLink, { to: '/realestate/bindtheowner' }, { default: () => '' }), key: "bindTheOwner",
            },
        ]
    },
    // 
    /* {
        label: "",
        key: "cost",
        icon: renderIcon(MoneyIcon),
        children: [
            {
                label: () => h(RouterLink, { to: '/' }, { default: () => '' }), key: "expenseItemSettings",
            },
            {
                label: () => h(RouterLink, { to: '/' }, { default: () => '' }), key: "feePackage",
            },
            {
                label: () => h(RouterLink, { to: '/' }, { default: () => '' }), key: "housingHarges",
            },
        ]
    }, */
    // 
    {
        label: "",
        key: "office",
        icon: renderIcon(BezierCurveSquareIcon),
        children: [
            {
                label: () => h(RouterLink, { to: '/office/annouce' }, { default: () => '' }), key: "annouce",
            },
            {
                label: () => h(RouterLink, { to: '/office/phonecomplaint' }, { default: () => '' }), key: "phoneComplaint",
            },
            {
                label: () => h(RouterLink, { to: '/office/touristregistration' }, { default: () => '' }), key: "touristRegistration",
            },
            {
                label: () => h(RouterLink, { to: '/office/ruleofactivity' }, { default: () => '' }), key: "ruleOfActivity",
            },
        ]
    },
    // 
    {
        label: "",
        key: "parking",
        icon: renderIcon(VehicleCarProfileLtrIcon),
        children: [
            {
                label: () => h(RouterLink, { to: '/parking/parkinglotmanagement' }, { default: () => '' }), key: "parkingLotManagement",
            },
            {
                label: () => h(RouterLink, { to: '/parking/parkingspacemanagement' }, { default: () => '' }), key: "parkingSpaceManagement",
            },
            {
                label: () => h(RouterLink, { to: '/parking/boothmanagement' }, { default: () => '' }), key: "boothManagement",
            },
            {
                label: () => h(RouterLink, { to: '/parking/ownervehicle' }, { default: () => '' }), key: "ownerVehicle",
            },
            {
                label: () => h(RouterLink, { to: '/parking/approachrecord' }, { default: () => '' }), key: "approachRecord",
            },
            {
                label: () => h(RouterLink, { to: '/parking/blackandwhitelist' }, { default: () => '' }), key: "blackAndWhiteList",
            },
            {
                label: () => h(RouterLink, { to: '/parking/remainingparkingspaces' }, { default: () => '' }), key: "remainingParkingSpaces",
            },
            {
                label: () => h(RouterLink, { to: '/parking/parkingspaceapplication' }, { default: () => '' }), key: "parkingSpaceApplication",
            },
        ]
    },
    // 
    {
        label: "",
        key: "repair",
        icon: renderIcon(CalendarEditIcon),
        children: [
            {
                label: () => h(RouterLink, { to: '/repair/warrantysettings' }, { default: () => '' }), key: "warrantySettings",
            },
            {
                label: () => h(RouterLink, { to: '/repair/telephonerepair' }, { default: () => '' }), key: "telephoneRepair",
            },
            {
                label: () => h(RouterLink, { to: '/repair/ticketpool' }, { default: () => '' }), key: "ticketPool",
            },
            {
                label: () => h(RouterLink, { to: '/' }, { default: () => '' }), key: "repairPending",
            },
            {
                label: () => h(RouterLink, { to: '/repair/repairhasbeendone' }, { default: () => '' }), key: "repairHasBeenDone",
            },
            {
                label: () => h(RouterLink, { to: '/repair/repairvisit' }, { default: () => '' }), key: "repairVisit",
            },
            {
                label: () => h(RouterLink, { to: '/repair/mandatoryreturnvisit' }, { default: () => '' }), key: "mandatoryReturnVisit",
            },
        ]
    },
    // 
    /* {
        label: "",
        key: "purchase",
        icon: renderIcon(ShoppingBagIcon),
        children: [
            {
                label: () => h(RouterLink, { to: '/' }, { default: () => '' }), key: "warehouseInformation",
            },
            {
                label: () => h(RouterLink, { to: '/' }, { default: () => '' }), key: "itemInformation",
            },
            {
                label: () => h(RouterLink, { to: '/' }, { default: () => '' }), key: "itemType",
            },
            {
                label: () => h(RouterLink, { to: '/' }, { default: () => '' }), key: "itemupplier",
            },
            {
                label: () => h(RouterLink, { to: '/' }, { default: () => '' }), key: "ItemSpecifications",
            },
            {
                label: () => h(RouterLink, { to: '/' }, { default: () => '' }), key: "purchaseRequisition",
            },
            {
                label: () => h(RouterLink, { to: '/' }, { default: () => '' }), key: "inboundAndOutboundDetails",
            },
            {
                label: () => h(RouterLink, { to: '/' }, { default: () => '' }), key: "itemUsageRecord",
            },
        ]
    }, */
    // 
    {
        label: "",
        key: "equipment",
        icon: renderIcon(DeveloperBoardIcon),
        children: [
            {
                label: () => h(RouterLink, { to: '/equipment/devicelocation' }, { default: () => '' }), key: "deviceLocation",
            },
            {
                label: () => h(RouterLink, { to: '/equipment/equipmenttype' }, { default: () => '' }), key: "equipmentType",
            },
            {
                label: () => h(RouterLink, { to: '/equipment/deviceinformation' }, { default: () => '' }), key: "deviceInformation",
            },
            {
                label: () => h(RouterLink, { to: '/equipment/accesscontrolequipment' }, { default: () => '' }), key: "accessControlEquipment",
            },
            {
                label: () => h(RouterLink, { to: '/equipment/vehiclebarrier' }, { default: () => '' }), key: "vehicleBarrier",
            },
            {
                label: () => h(RouterLink, { to: '/equipment/monitoringequipment' }, { default: () => '' }), key: "monitoringEquipment",
            },
            {
                label: () => h(RouterLink, { to: '/equipment/attendancemachine' }, { default: () => '' }), key: "attendanceMachine",
            },
            {
                label: () => h(RouterLink, { to: '/equipment/opendoorrecord' }, { default: () => '' }), key: "openDoorRecord",
            },
            {
                label: () => h(RouterLink, { to: '/equipment/employeeaccesscontrolauthorization' }, { default: () => '' }), key: "employeeAccessControlAuthorization",
            },
            {
                label: () => h(RouterLink, { to: '/equipment/requestkey' }, { default: () => '' }), key: "requestKey",
            },
        ]
    },
    // 
    {
        label: "",
        key: "epidemic",
        icon: renderIcon(SyringeIcon),
        children: [
            {
                label: () => h(RouterLink, { to: '/epidemic/epidemicsettings' }, { default: () => '' }), key: "epidemicSettings",
            },
            {
                label: () => h(RouterLink, { to: '/epidemic/reportbacktotheprovince' }, { default: () => '' }), key: "reportBackToTheProvince",
            },
            {
                label: () => h(RouterLink, { to: '/epidemic/epidemicreport' }, { default: () => '' }), key: "epidemicReport",
            }
        ]
    }
];
// 
const leftWidth = 180;
// 
const leftOpen = ref(true);
// 
const leftStatus = ref(false);
// 
const leftBlack = ref(true);
// 
const dark = inject('dark');
// 
const to = router.currentRoute._value;

// 
window.onresize = () => {
    return (() => {
        initWindow();
    })()
}

// 
const initWindow = () => {
    // 
    let width = document.body.clientWidth
    //  1024 
    if (width < 1024) {
        leftOpen.value = false;
        leftStatus.value = false;
    } else {
        leftOpen.value = true;
        leftStatus.value = true;
    }
    // 
    updateTitle(to)
}
// 
initWindow();

// 
function updateTitle(to) {
    // 
    title.value = []
    // 
    if (to.matched.length === 2) {
        title.value.push({
            lable: ''
        })
    } else {
        // 
        for (let toInfo of to.matched) {
            //  home  main 
            if (toInfo.name !== 'home' && toInfo.name !== 'main') {
                // 
                title.value.push({
                    lable: toInfo.meta.title
                })
            }
        }
    }
}

// 
const searchModal = ref(false)
// 
const routesRoot = router.options.routes
// 
const searchText = ref('')
// 
const searchList = ref([])
// 
watch(searchText, () => {
    searchList.value = []
    if (searchText.value.trim() !== '') {
        findRouters('', '', searchText.value, routesRoot, searchList.value)
    }
})

// 
function findRouters(name, to, str, routesRoot, list) {
    for (let info of routesRoot) {
        if (info.children != null || info.meta.title.includes(str) || info.name === 'main') {
            const title = info.name === 'main' ? '' : (name === '' ? '' : ' / ') + info.meta.title;
            const path = info.path.indexOf("/") !== -1 ? info.path === '/' ? '' : info.path : "/" + info.path;
            if (info.children != null) {
                findRouters(name + title, to + path, str, info.children, list)
            } else {
                list.push({
                    name: name + title,
                    to: to + path
                })
            }
        }
    }
}
// 
function openPage(name) {
    router.push({ name })
}

</script>
<script>
export default {
    name: 'MainLayout'
}
</script>

<style scoped>
.app {
    display: flex;
    flex-direction: row;
}

.app>.left {
    flex: none;
    z-index: 1;
    height: 100vh;
    /* box-shadow: 5px 0 10px var(--divider-color); */
    border-right: 1px solid var(--divider-color);
}

.app>.right {
    flex: 1;
    height: 100vh;
    display: flex;
    flex-direction: column;
}

/*  */
@import url(../assets/style/page-animation.css);
</style>